import React, { FC } from 'react'
import { useRouter } from 'next/router'
import { LidoLinkProps } from '@lidofinance/next-ui-primitives'

import { NavigationLinkStyle } from './styles'

export type NavigationLinkProps = LidoLinkProps & {
  icon: React.ReactNode
  title: string
  href: string
}

export const NavigationLink: FC<NavigationLinkProps> = ({ icon, title, href, ...rest }) => {
  const { pathname } = useRouter()

  // compare without query string
  const active = pathname === href.split('?')[0]

  return (
    // @ts-expect-error Research why types of property 'as' are incompatible.
    <NavigationLinkStyle {...rest} href={href} $active={active}>
      {/* Parcel don't support React fragment short syntax without hand configuration */}
      <React.Fragment>
        {icon}
        <span>{title}</span>
      </React.Fragment>
    </NavigationLinkStyle>
  )
}
