import { FC, ReactNode } from 'react'
import Link from 'next/link'
import { LidoLogo } from '@lidofinance/lido-ui'

import { HeaderActions } from './components/headerActions'
import { HeaderStyle, HeaderLogoStyle } from './styles'

export type HeaderProps = {
  headerActions: ReactNode
}

export const Header: FC<HeaderProps> = ({ headerActions, children, ...rest }) => (
  <HeaderStyle {...rest} size="full" forwardedAs="header">
    <HeaderLogoStyle>
      <Link href="/" passHref legacyBehavior>
        <LidoLogo />
      </Link>
    </HeaderLogoStyle>
    {children}
    <HeaderActions leftSlot={headerActions} />
  </HeaderStyle>
)
